@mixin weatherData {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}

@mixin wrapperRenderWatherData {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-around;
}

@mixin time {
  color: black;
  font-size: 24px;
}

@mixin iconWetherData {
  height: 150px;
  width: 150px;
}
